<template>
<div class="myBody">
    <div class="bg">
        <img
            src="../assets/bg_2.jpg"
            alt=""
        ></div>
    <div class="content">
      <div class="title">
        <h2 >支   部   选    择</h2>
        <br>
      </div>
      <div class="depatementTable">
          <v-table
           is-vertical-resize
             is-horizontal-resize
            style="width:100%;height:100%"
               
             :columns="columns"
             :table-data="tableData"
             :row-click="choose"
             :show-vertical-border="true"
               row-hover-color="#eee"
            row-click-color="#edf7ff"
    ></v-table>
      </div>
      
        <div class="bottom-button">
            <div>
                <button
                @click="next"
                  
                    class="button-1"
                >确定</button>
            </div>
            <div>
                <router-link
                    to="/"
                    class="button-1"
                >返回</router-link>
            </div>

        </div>

    </div>
    <div class="dia_ques" v-if="bool_diaOne">
			<quesDia_one :messageOne="messageOne" v-if="messageOne" @determine="closePopOne"></quesDia_one>
	</div>

    <div class="showDia_redLine" v-if="bool_diaTwo">
			<loginDia :messageTwo="messageTwo" v-if="messageTwo" @determine="closePopTwo" @backFromLogin="backFromLogin"></loginDia>
	</div>
</div>
</template>


<script>
	import loginDia from './diaLogin'
	import quesDia_one from './quesDia_one'
export default {
	components: {
//			redLineDia,
//			quesDia,
            loginDia,
			quesDia_one
		},
	
  name: "choiceDepartment",
  methods: {
    choose(rowIndex, rowData, column) {
      this.messageTwo = rowData;
    },
    next() {
      if (this.messageTwo.PartyBranchID != -1) {
      	
        this.showPopTwo(this.messageTwo);
      } else {
      	this.showPopOne(this.messageA);//请先选择支部
      }
    },
    
    backFromLogin(){
    	this.closePopTwo();
    },
    
    showPopTwo(message){
    	this.bool_diaTwo=true;
    	this.messageTwo=message;
    	
    },
    closePopTwo(){
    	this.bool_diaTwo=false;
    },
    showPopOne(message){
    	this.bool_diaOne=true;
    	this.messageOne=message;
    	
    },
    closePopOne(){
    	this.bool_diaOne=false;
    },
    listUpdate(list) {
      this.tableData = list;
    },
    closeDoor() {}
  },
  created: function() {
    this.$ajax({
      method: "get",
      url: "http://202.206.249.16:8080/dxpj/zzp/get_party_selected"
    })
      .then(res => {
        //info

        res = res.data;

        if (res.status.code == 100) {
          var list = res.info;
        } else if (res.status.code == 103) {
          var list = [
            {
              PartyBranchID: "-1",
              PartyBranchName: "暂无",
              City: "暂无"
            }
          ];
        }

        this.listUpdate(list);
      })
      .catch(function(err) {
        console.log(err);
      });
  },
data() {
    return {
    	messageA:"请先选择支部",
    	
    	
      bool_diaOne:false,
      bool_diaTwo:false,
      messageOne:'',
      messageTwo:{
      	  PartyBranchID: -1,
          PartyBranchName: "test1",
          City: "北京"
      },
      chooseId: -1,
      
      tableData: [
        {
          PartyBranchID: "1",
          PartyBranchName: "test1",
          City: "北京"
        },
        {
          PartyBranchID: "2",
          PartyBranchName: "test1",
          City: "秦皇岛"
        },
        {
          PartyBranchID: "3",
          PartyBranchName: "test1",
          City: "河北"
        },
        {
          PartyBranchID: "4",
          PartyBranchName: "test1",
          City: "安徽"
        },
        {
          PartyBranchID: "5",
          PartyBranchName: "test1",
          City: "上海"
        }
      ],
      columns: [
        {
          field: "PartyBranchID",
          title: "支部编号",
          width: 100,
          titleAlign: "center",
          columnAlign: "center",
          isResize: true
        },
        {
          field: "PartyBranchName",
          title: "支部名称",
          width: 600,
          titleAlign: "center",
          columnAlign: "center",
          isResize: true
        },
        {
          field: "City",
          title: "支部所在省",
          width: 100,
          titleAlign: "center",
          columnAlign: "center",
          isResize: true
        }
      ]
    };
}
};
</script>

<style scoped>
	.dia_ques {
		position: absolute;
		left: 725px;
		top: 250px;
		z-index: 100;
	}
	.showDia_redLine {
		/*position: absolute;
		left: 350px;
		top: 50px;
		z-index: 100;*/
		position: fixed;
		top: 0%;
		width: 100%;
		height: 100%;
		z-index: 200;
		background: rgba(0, 0, 0, 0.5) !important;
		/* 兼容ie几不知道，好像ie6 */
		background: #000;
		filter: Alpha(opacity=60);
	}
.bottom-button {
  top: 75% !important;
}
.depatementTable {
  margin: auto;
  width: 70%;
  height: 60%;
  
}
</style>



